.App {
  font-family: "Poppins", sans-serif;
  background-color: #f8f8f8;
  background-image: url("https://res.cloudinary.com/dmylma7bf/image/upload/v1742571730/background-image3_sbbmos.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 100vh;
  align-items: center;
}

.Main {
  padding-top: 100px;
}

.Content {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 2px;
  border: 1px solid #dadadf;
}

.FillerImage {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.Image {
  max-height: 40vh;
  max-width: 100%;
  object-fit: contain;
}

h1 {
  font-family: "Exo 2", sans-serif;
  font-size: 3rem;
  color: #fff;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5), 0 0 25px rgba(0, 255, 255, 0.3),
    0 0 50px rgba(0, 255, 255, 0.3);
  letter-spacing: 2px;
  text-transform: uppercase;
  background: linear-gradient(
    45deg,
    rgba(0, 255, 255, 0.4),
    rgba(255, 0, 255, 0.4)
  );
  -webkit-background-clip: text;
  background-clip: text;
  animation: glowAnimation 1.5s ease-in-out infinite alternate;
  margin-bottom: 20px;
  padding-left: 10px;
  text-align: center;
}

@keyframes glowAnimation {
  0% {
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7), 0 0 25px rgba(0, 255, 255, 0.7),
      0 0 50px rgba(0, 255, 255, 0.7);
  }
  50% {
    text-shadow: 2px 2px 15px rgba(0, 0, 0, 0.7), 0 0 35px rgba(0, 255, 255, 1),
      0 0 70px rgba(0, 255, 255, 1);
  }
  100% {
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7), 0 0 25px rgba(0, 255, 255, 0.7),
      0 0 50px rgba(0, 255, 255, 0.7);
  }
}

@media (min-width: 576px) {
  h1 {
    font-size: 4rem;
  }
}

* {
  box-sizing: border-box;
}

a {
  color: #555555;
  transition: color ease-in-out 0.3s;
}

a:hover {
  text-decoration: none;
}

a:hover i {
  color: #2142b2;
  transition: color ease-in-out 0.3s;
}

input[type="file"] {
  display: none;
}

i {
  color: #cfced3;
  font-size: 1.6rem;
  padding: 7px;
  vertical-align: middle;
}

i:hover {
  cursor: pointer;
  transition: color ease-in-out 0.3s;
  color: #2142b2;
}

figure {
  text-align: center;
  display: inline-block;
}

/* scrollbar */
*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: #f1f1f1;
}
*::-webkit-scrollbar-thumb {
  background: #888;
}
*::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.PageNotFound {
  color: #ffffff;
}